<template>
  <div class="mdc-layout-grid">
    <div class="mdc-layout-grid__inner">
      <div class="mdc-layout-grid__cell--span-12"><h3 class="mdc-typography--headline3">Gebhren</h3></div>
      <form @submit.prevent="addFee" class="mdc-layout-grid__cell--span-12">
        <div class="mdc-layout-grid__inner">
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="text" id="airplane-type" v-model="fee.name" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="airplane-type" class="mdc-floating-label">Flugzeugtyp</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="number" min="0" step="1000" id="starting-fee" v-model="fee.startingFee" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="starting-fee" class="mdc-floating-label">Startgebhr</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="number" min="0" step="1000" id="landing-fee" v-model="fee.landingFee" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="landing-fee" class="mdc-floating-label">Landegebhr</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="number" min="0" step="1000" id="parking-fee" v-model="fee.parkingFee" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="parking-fee" class="mdc-floating-label">Parkgebhr</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <button type="submit" class="mdc-button">
              <i class="material-icons mdc-button__icon" aria-hidden="true">add</i>
              <span class="mdc-button__label">Anlegen</span>
            </button>
          </div>
        </div>
      </form>
      <div class="mdc-layout-grid__cell--span-12">
        <div class="mdc-data-table">
          <table class="mdc-data-table__table" aria-label="Dessert calories">
            <thead>
              <tr class="mdc-data-table__header-row">
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Flugzeugtyp</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Startgebhr</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Landegebhr</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Parkgebhr</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Aktionen</th>
              </tr>
            </thead>
            <tbody class="mdc-data-table__content">
              <tr v-for="(fee, index) in fees" :key="index" class="mdc-data-table__row">
                <td class="mdc-data-table__cell">{{ fee.name }}</td>
                <td class="mdc-data-table__cell">{{ fee.startingFee }}</td>
                <td class="mdc-data-table__cell">{{ fee.landingFee }}</td>
                <td class="mdc-data-table__cell">{{ fee.parkingFee }}</td>
                <td class="mdc-data-table__cell">
                  <button v-on:click="removeFee(fee.id)" class="mdc-icon-button material-icons">remove</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { MDCDataTable } from '@material/data-table'
import { MDCRipple } from '@material/ripple'
import { MDCTextField } from '@material/textfield'
import { MDCSnackbar } from '@material/snackbar'

export default {
  name: 'Fees',
  data () {
    return {
      fee: this.newFee(),
      fees: []
    }
  },
  methods: {
    newFee: function () {
      return {
        name: '',
        startingFee: 0.0,
        landingFee: 0.0,
        parkingFee: 0.0
      }
    },
    getFees: async function () {
      const response = await fetch('http://localhost:8080/api/airplane-types')
      const data = await response.json()
      this.fees = data.payload
    },
    addFee: async function () {
      const init = {
        headers: { Accept: 'application/json', 'Content-Type': 'application/json' },
        method: 'POST',
        body: JSON.stringify(this.fee)
      }
      const response = await fetch('http://localhost:8080/api/airplane-types', init)
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = Object.keys(data)
        .map(key => data[key])
        .join(', ')
      snackbar.open()
      if (response.ok) {
        this.fee = this.newFee()
        this.getFees()
      }
    },
    removeFee: async function (id) {
      const response = await fetch(`http://localhost:8080/api/airplane-types/${id}`, { method: 'DELETE' })
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = data.message
      snackbar.open()
      if (response.ok) {
        this.getFees()
      }
    }
  },
  mounted () {
    MDCDataTable.attachTo(document.querySelector('.mdc-data-table'))
    for (const clickable of document.querySelectorAll('.mdc-button')) {
      MDCRipple.attachTo(clickable)
    }
    for (const textField of document.querySelectorAll('.mdc-text-field')) {
      MDCTextField.attachTo(textField)
    }
    this.getFees()
  }
}
</script>

<style>
@import "~@material/data-table/dist/mdc.data-table.css";
@import "~@material/button/dist/mdc.button.css";
@import "~@material/textfield/dist/mdc.textfield.css";
</style>
