<template>
  <div class="mdc-layout-grid">
    <div class="mdc-layout-grid__inner">
      <div class="mdc-layout-grid__cell--span-12"><h3 class="mdc-typography--headline3">Flugzeuge</h3></div>
      <form @submit.prevent="addAirplane" class="mdc-layout-grid__cell--span-12">
        <div class="mdc-layout-grid__inner">
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-select mdc-select--outlined grid-cell-select">
              <i class="mdc-select__dropdown-icon"></i>
              <select v-model="airplane.airline.id" class="mdc-select__native-control">
                <option value="" disabled selected></option>
                <option v-for="(airline, index) in airlines" :key="index" :value="airline.id">
                  {{ airline.name }}
                </option>
              </select>
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label class="mdc-floating-label">Fluggesellschaft</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-select mdc-select--outlined grid-cell-select">
              <i class="mdc-select__dropdown-icon"></i>
              <select v-model="airplane.airplaneType.id" class="mdc-select__native-control">
                <option value="" disabled selected></option>
                <option v-for="(type, index) in types" :key="index" :value="type.id">
                  {{ type.name }}
                </option>
              </select>
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label class="mdc-floating-label">Flugzeugtyp</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="text" id="registration" v-model="airplane.registration" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="registration" class="mdc-floating-label">Luftfahrtkennzeichen</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <button type="submit" class="mdc-button">
              <i class="material-icons mdc-button__icon" aria-hidden="true">add</i>
              <span class="mdc-button__label">Anlegen</span>
            </button>
          </div>
        </div>
      </form>
      <div class="mdc-layout-grid__cell--span-12">
        <div class="mdc-data-table">
          <table class="mdc-data-table__table" aria-label="Dessert calories">
            <thead>
              <tr class="mdc-data-table__header-row">
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Fluggesellschaft</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Flugzeugtyp</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Luftfahrtkennzeichen</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Aktionen</th>
              </tr>
            </thead>
            <tbody class="mdc-data-table__content">
              <tr v-for="(airplane, index) in airplanes" :key="index" class="mdc-data-table__row">
                <td class="mdc-data-table__cell">{{ airplane.airline.name }}</td>
                <td class="mdc-data-table__cell">{{ airplane.airplaneType.name }}</td>
                <td class="mdc-data-table__cell">{{ airplane.registration }}</td>
                <td class="mdc-data-table__cell">
                  <button v-on:click="removeAirplane(airplane.id)" class="mdc-icon-button material-icons">remove</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { MDCDataTable } from '@material/data-table'
import { MDCRipple } from '@material/ripple'
import { MDCTextField } from '@material/textfield'
import { MDCSelect } from '@material/select'
import { MDCSnackbar } from '@material/snackbar'

export default {
  name: 'Airplanes',
  data () {
    return {
      airplane: this.newAirplane(),
      airplanes: [],
      airlines: [],
      types: []
    }
  },
  methods: {
    newAirplane: function () {
      return {
        airline: { id: 0 },
        airplaneType: { id: 0 },
        registration: ''
      }
    },
    getAirlines: async function () {
      const response = await fetch('http://localhost:8080/api/airlines')
      const data = await response.json()
      this.airlines = data.payload
    },
    getAirplaneTypes: async function () {
      const response = await fetch('http://localhost:8080/api/airplane-types')
      const data = await response.json()
      this.types = data.payload
    },
    getAirplanes: async function () {
      const response = await fetch('http://localhost:8080/api/airplanes')
      const data = await response.json()
      this.airplanes = data.payload
    },
    addAirplane: async function () {
      const init = {
        headers: { Accept: 'application/json', 'Content-Type': 'application/json' },
        method: 'POST',
        body: JSON.stringify(this.airplane)
      }
      const response = await fetch('http://localhost:8080/api/airplanes', init)
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = Object.keys(data)
        .map(key => data[key])
        .join(', ')
      snackbar.open()
      if (response.ok) {
        this.airplane = this.newAirplane()
        this.getAirplanes()
      }
    },
    removeAirplane: async function (id) {
      const response = await fetch(`http://localhost:8080/api/airplanes/${id}`, { method: 'DELETE' })
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = data.message
      snackbar.open()
      if (response.ok) {
        this.getAirplanes()
      }
    }
  },
  mounted () {
    MDCDataTable.attachTo(document.querySelector('.mdc-data-table'))
    for (const clickable of document.querySelectorAll('.mdc-button')) {
      MDCRipple.attachTo(clickable)
    }
    for (const textField of document.querySelectorAll('.mdc-text-field')) {
      MDCTextField.attachTo(textField)
    }
    for (const select of document.querySelectorAll('.mdc-select')) {
      MDCSelect.attachTo(select)
    }
    this.getAirlines()
    this.getAirplaneTypes()
    this.getAirplanes()
  }
}
</script>

<style>
@import "~@material/data-table/dist/mdc.data-table.css";
@import "~@material/button/dist/mdc.button.css";
@import "~@material/textfield/dist/mdc.textfield.css";
@import "~@material/select/dist/mdc.select.css";

.grid-cell-select {
  width: 100%;
}
</style>
