<template>
  <div class="mdc-card mdc-card--outlined text-card-content">
    <div class="mdc-layout-grid__inner">
      <h4 class="mdc-typography--headline4 mdc-layout-grid__cell--span-12">Rechnungsbetrge</h4>
      <div class="mdc-layout-grid__cell--span-12">
        <div class="mdc-data-table">
          <table class="mdc-data-table__table" aria-label="Betrge">
            <thead>
              <tr class="mdc-data-table__header-row">
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Year Month</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Fluggesellschaft</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Startgebhr</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Landegebhr</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Parkgebhr</th>
              </tr>
            </thead>
            <tbody class="mdc-data-table__content">
              <tr v-for="(invoice, index) in invoices" :key="index" class="mdc-data-table__row">
                <td class="mdc-data-table__cell">{{ invoice.date }}</td>
                <td class="mdc-data-table__cell">{{ invoice.airline.name }}</td>
                <td class="mdc-data-table__cell">{{ invoice.startingFee }}</td>
                <td class="mdc-data-table__cell">{{ invoice.landingFee }}</td>
                <td class="mdc-data-table__cell">{{ invoice.parkingFee }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { MDCDataTable } from '@material/data-table'

export default {
  name: 'Invoice',
  data () {
    return {
      invoices: []
    }
  },
  methods: {
    getInvoices: async function () {
      const response = await fetch('http://localhost:8080/api/invoices')
      const data = await response.json()
      this.invoices = data.payload
    }
  },
  mounted () {
    for (const table of document.querySelectorAll('.mdc-data-table')) {
      MDCDataTable.attachTo(table)
    }
    this.getInvoices()
  }
}
</script>

<style>
@import "~@material/card/dist/mdc.card.css";
@import "~@material/data-table/dist/mdc.data-table.css";

.grid-cell-select {
  width: 100%;
}

.text-card-content {
  padding: 16px;
}
</style>
