<template>
  <div class="mdc-layout-grid">
    <div class="mdc-layout-grid__inner">
      <div class="mdc-layout-grid__cell--span-12"><h3 class="mdc-typography--headline3">Mitarbeiter</h3></div>
      <form @submit.prevent="addEmployee" class="mdc-layout-grid__cell--span-12">
        <div class="mdc-layout-grid__inner">
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="text" id="first-name" v-model="employee.firstName" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="first-name" class="mdc-floating-label">Vorname</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="text" id="last-name" v-model="employee.lastName" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="last-name" class="mdc-floating-label">Nachname</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-3">
            <div class="mdc-select mdc-select--outlined">
              <i class="mdc-select__dropdown-icon"></i>
              <select required v-model="employee.job" class="mdc-select__native-control">
                <option value="" disabled selected></option>
                <option value="AIRPORT_CLERK">
                  {{ getJobTitle("AIRPORT_CLERK") }}
                </option>
                <option value="AIR_TRAFFIC_CONTROLLER">
                  {{ getJobTitle("AIR_TRAFFIC_CONTROLLER") }}
                </option>
              </select>
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label class="mdc-floating-label">Job</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <button type="submit" class="mdc-button">
              <i class="material-icons mdc-button__icon" aria-hidden="true">add</i>
              <span class="mdc-button__label">Anlegen</span>
            </button>
          </div>
        </div>
      </form>
      <div class="mdc-layout-grid__cell--span-12">
        <div class="mdc-data-table">
          <table class="mdc-data-table__table" aria-label="Dessert calories">
            <thead>
              <tr class="mdc-data-table__header-row">
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Vorname</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Nachname</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Beruf</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Aktionen</th>
              </tr>
            </thead>
            <tbody class="mdc-data-table__content">
              <tr v-for="(employee, index) in employees" :key="index" class="mdc-data-table__row">
                <td class="mdc-data-table__cell">{{ employee.firstName }}</td>
                <td class="mdc-data-table__cell">{{ employee.lastName }}</td>
                <td class="mdc-data-table__cell">{{ getJobTitle(employee.job) }}</td>
                <td class="mdc-data-table__cell">
                  <button v-on:click="removeEmployee(employee.id)" class="mdc-icon-button material-icons">remove</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { MDCDataTable } from '@material/data-table'
import { MDCRipple } from '@material/ripple'
import { MDCTextField } from '@material/textfield'
import { MDCSelect } from '@material/select'
import { MDCSnackbar } from '@material/snackbar'

export default {
  name: 'Employees',
  data () {
    return {
      employee: this.newEmployee(),
      employees: []
    }
  },
  methods: {
    getJobTitle: function (job) {
      if (job === 'AIR_TRAFFIC_CONTROLLER') return 'Fluglotse'
      if (job === 'AIRPORT_CLERK') return 'Angestellter der Flughafenverwaltung'
      return job
    },
    newEmployee: function () {
      return {
        firstName: '',
        lastName: '',
        job: ''
      }
    },
    getEmployees: async function () {
      const response = await fetch('http://localhost:8080/api/employees')
      const data = await response.json()
      this.employees = data.payload
    },
    addEmployee: async function () {
      const init = {
        headers: { Accept: 'application/json', 'Content-Type': 'application/json' },
        method: 'POST',
        body: JSON.stringify(this.employee)
      }
      const response = await fetch('http://localhost:8080/api/employees', init)
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = data.message
      snackbar.open()
      if (response.ok) {
        this.employee = this.newEmployee()
        this.getEmployees()
      }
    },
    removeEmployee: async function (id) {
      const response = await fetch(`http://localhost:8080/api/employees/${id}`, { method: 'DELETE' })
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = Object.keys(data)
        .map(key => data[key])
        .join(', ')
      snackbar.open()
      if (response.ok) {
        this.getEmployees()
      }
    }
  },
  mounted () {
    MDCDataTable.attachTo(document.querySelector('.mdc-data-table'))
    for (const clickable of document.querySelectorAll('.mdc-button')) {
      MDCRipple.attachTo(clickable)
    }
    for (const textField of document.querySelectorAll('.mdc-text-field')) {
      MDCTextField.attachTo(textField)
    }
    for (const select of document.querySelectorAll('.mdc-select')) {
      MDCSelect.attachTo(select)
    }
    this.getEmployees()
  }
}
</script>

<style>
@import "~@material/data-table/dist/mdc.data-table.css";
@import "~@material/button/dist/mdc.button.css";
@import "~@material/textfield/dist/mdc.textfield.css";
@import "~@material/select/dist/mdc.select.css";
</style>
