<template>
  <div class="mdc-card mdc-card--outlined text-card-content">
    <div class="mdc-layout-grid__inner">
      <h4 v-if="flightStatus === 'TAKE_OFF'" class="mdc-typography--headline4 mdc-layout-grid__cell--span-12">Start</h4>
      <h4 v-else class="mdc-typography--headline4 mdc-layout-grid__cell--span-12">Landung</h4>
      <form @submit.prevent="save" class="mdc-layout-grid__cell--span-12">
        <div class="mdc-layout-grid__inner">
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-select mdc-select--outlined grid-cell-select">
              <i class="mdc-select__dropdown-icon"></i>
              <select v-model="entry.airplane.id" class="mdc-select__native-control">
                <option value disabled selected></option>
                <option v-for="(airplane, index) in airplanes" :key="index" :value="airplane.id">{{ airplane.registration }}</option>
              </select>
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label class="mdc-floating-label">Flugzeug</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="text" id="entry-date" v-model="entry.time" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="entry-date" class="mdc-floating-label">Uhrzeit</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-text-field mdc-text-field--outlined">
              <input type="number" min="0" step="1" id="entry-duration" v-model="entry.duration" class="mdc-text-field__input" />
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label for="entry-duration" class="mdc-floating-label">Dauer</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <div class="mdc-select mdc-select--outlined grid-cell-select">
              <i class="mdc-select__dropdown-icon"></i>
              <select v-model="entry.runway" class="mdc-select__native-control">
                <option value disabled selected></option>
                <option v-for="(runway, index) in runways" :key="index" :value="runway">{{ runway }}</option>
              </select>
              <div class="mdc-notched-outline">
                <div class="mdc-notched-outline__leading"></div>
                <div class="mdc-notched-outline__notch">
                  <label class="mdc-floating-label">Bahn</label>
                </div>
                <div class="mdc-notched-outline__trailing"></div>
              </div>
            </div>
          </div>
          <div class="mdc-layout-grid__cell--span-2">
            <button type="submit" class="mdc-button">
              <i class="material-icons mdc-button__icon" aria-hidden="true">add</i>
              <span class="mdc-button__label">speichern</span>
            </button>
          </div>
        </div>
      </form>
      <div class="mdc-layout-grid__cell--span-12">
        <div class="mdc-data-table">
          <table class="mdc-data-table__table" aria-label="Vorgnge">
            <thead>
              <tr class="mdc-data-table__header-row">
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Flugzeug</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Uhrzeit</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Dauer</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Bahn</th>
                <th class="mdc-data-table__header-cell" role="columnheader" scope="col">Fluglotse</th>
              </tr>
            </thead>
            <tbody class="mdc-data-table__content">
              <tr v-for="(entry, index) in entries" :key="index" class="mdc-data-table__row">
                <td class="mdc-data-table__cell">{{ entry.airplane.registration }}</td>
                <td class="mdc-data-table__cell">{{ entry.time }}</td>
                <td class="mdc-data-table__cell">{{ entry.duration }}</td>
                <td class="mdc-data-table__cell">{{ entry.runway }}</td>
                <td class="mdc-data-table__cell">
                  {{ entry.controller.firstName }}
                  {{ entry.controller.lastName }}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { MDCDataTable } from '@material/data-table'
import { MDCRipple } from '@material/ripple'
import { MDCTextField } from '@material/textfield'
import { MDCSelect } from '@material/select'
import { MDCSnackbar } from '@material/snackbar'

export default {
  name: 'Schedule',
  props: {
    controller: {},
    airplanes: {},
    flightStatus: String
  },
  data () {
    return {
      entry: this.newEntry(),
      entries: [],
      runways: []
    }
  },
  watch: {
    'entry.time': async function (val, oldVal) {
      if (val !== oldVal) {
        this.getRunways()
      }
    },
    'entry.duration': async function (val, oldVal) {
      if (val !== oldVal) {
        this.getRunways()
      }
    }
  },
  methods: {
    newEntry: function () {
      const date = new Date()
      const month = date.getMonth() + 1
      const day = date.getDate()
      const year = date.getFullYear()
      const hour = date.getHours()
      const minute = date.getMinutes()

      return {
        airplane: { id: 0 },
        runway: 0,
        time: `${year}-${month < 10 ? '0' : ''}${month}-${day < 10 ? '0' : ''}${day}T${hour < 10 ? '0' : ''}${hour}:${
          minute < 10 ? '0' : ''
        }${minute}`,
        duration: 20,
        flightStatus: this.flightStatus
      }
    },
    getEntries: async function () {
      const response = await fetch(`http://localhost:8080/api/schedules?flight-status=${this.flightStatus}`)
      const data = await response.json()
      this.entries = data.payload
    },
    getRunways: async function () {
      const response = await fetch(
        `http://localhost:8080/api/schedules/free-runways?time=${this.entry.time}&duration=${this.entry.duration}`
      )
      const data = await response.json()
      this.runways = data.payload
    },
    save: async function () {
      this.entry.controller = { id: this.controller }
      const init = {
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        method: 'POST',
        body: JSON.stringify(this.entry)
      }
      const response = await fetch('http://localhost:8080/api/schedules', init)
      const data = await response.json()
      const snackbar = MDCSnackbar.attachTo(document.querySelector('.mdc-snackbar'))
      snackbar.labelText = Object.keys(data)
        .map(key => data[key])
        .join(', ')
      snackbar.open()
      if (response.ok) {
        this.entry = this.newEntry()
        this.getEntries()
      }
    }
  },
  mounted () {
    for (const table of document.querySelectorAll('.mdc-data-table')) {
      MDCDataTable.attachTo(table)
    }
    for (const clickable of document.querySelectorAll('.mdc-button')) {
      MDCRipple.attachTo(clickable)
    }
    for (const textField of document.querySelectorAll('.mdc-text-field')) {
      MDCTextField.attachTo(textField)
    }
    for (const select of document.querySelectorAll('.mdc-select')) {
      MDCSelect.attachTo(select)
    }
    this.getEntries()
    this.getRunways()
  }
}
</script>

<style>
@import "~@material/card/dist/mdc.card.css";
@import "~@material/data-table/dist/mdc.data-table.css";
@import "~@material/button/dist/mdc.button.css";
@import "~@material/textfield/dist/mdc.textfield.css";
@import "~@material/select/dist/mdc.select.css";

.grid-cell-select {
  width: 100%;
}

.text-card-content {
  padding: 16px;
}
</style>
