<template>
  <div id="app" class="mdc-typography">
    <header class="mdc-top-app-bar">
      <div class="mdc-top-app-bar__row">
        <section class="mdc-top-app-bar__section mdc-top-app-bar__section--align-start">
          <button class="material-icons mdc-top-app-bar__navigation-icon mdc-icon-button">menu</button>
          <span class="mdc-top-app-bar__title">Airport</span>
        </section>
      </div>
    </header>
    <aside class="mdc-drawer mdc-drawer--dismissible mdc-top-app-bar--fixed-adjust">
      <div class="mdc-drawer__content">
        <div class="mdc-list">
          <router-link class="mdc-list-item mdc-list-item--activated" aria-current="page" to="/">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">home</i>
            <span class="mdc-list-item__text">bersicht</span>
          </router-link>
          <hr class="mdc-list-divider" />
          <h6 class="mdc-list-group__subheader">Stammdaten</h6>
          <router-link class="mdc-list-item" to="/employee">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">group</i>
            <span class="mdc-list-item__text">Mitarbeiter</span>
          </router-link>
          <router-link class="mdc-list-item" to="/fee">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">attach_money</i>
            <span class="mdc-list-item__text">Gebhren</span>
          </router-link>
          <router-link class="mdc-list-item" to="/airline">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">business</i>
            <span class="mdc-list-item__text">Fluggesellschaften</span>
          </router-link>
          <router-link class="mdc-list-item" to="/airplane">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">local_airport</i>
            <span class="mdc-list-item__text">Flugzeuge</span>
          </router-link>
          <hr class="mdc-list-divider" />
          <router-link class="mdc-list-item" to="/validation">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">bug_report</i>
            <span class="mdc-list-item__text">Validation</span>
          </router-link>
          <router-link class="mdc-list-item" to="/help">
            <i class="material-icons mdc-list-item__graphic" aria-hidden="true">help</i>
            <span class="mdc-list-item__text">Hilfe</span>
          </router-link>
        </div>
      </div>
    </aside>
    <div class="mdc-drawer-app-content mdc-top-app-bar--fixed-adjust">
      <main class="main-content" id="main-content">
        <router-view />
      </main>
    </div>
    <div class="mdc-snackbar">
      <div class="mdc-snackbar__surface">
        <div class="mdc-snackbar__label" role="status" aria-live="polite"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { MDCTopAppBar } from '@material/top-app-bar'
import { MDCDrawer } from '@material/drawer'

export default {
  mounted () {
    const topAppBar = MDCTopAppBar.attachTo(document.querySelector('.mdc-top-app-bar'))
    const drawer = MDCDrawer.attachTo(document.querySelector('.mdc-drawer'))
    const listEl = document.querySelector('.mdc-drawer .mdc-list')
    const mainContentEl = document.querySelector('.main-content')

    topAppBar.setScrollTarget(document.getElementById('main-content'))
    topAppBar.listen('MDCTopAppBar:nav', () => {
      drawer.open = !drawer.open
    })

    listEl.addEventListener('click', event => {
      const interactiveElement = mainContentEl.querySelector('input, button')
      if (interactiveElement) {
        interactiveElement.focus()
      }
    })

    document.body.addEventListener('MDCDrawer:closed', () => {
      mainContentEl.querySelector('input, button').focus()
    })
  }
}
</script>

<style>
@import "~@material/typography/dist/mdc.typography.css";
@import "~@material/top-app-bar/dist/mdc.top-app-bar.css";
@import "~@material/icon-button/dist/mdc.icon-button.css";
@import "~@material/drawer/dist/mdc.drawer.css";
@import "~@material/list/dist/mdc.list.css";
@import "~@material/snackbar/dist/mdc.snackbar.css";

.mdc-top-app-bar {
  z-index: 7;
}
</style>
