      const call = async function (url, method, body) {
        if (method === 'POST') {
          const init = {
            headers: {
              Accept: 'application/json',
              'Content-Type': 'application/json'
            },
            method: method,
            body: JSON.stringify(body)
          }
          return await fetch(url, init)
        } else {
          const init = {
            headers: {
              Accept: 'application/json'
            },
            method: method
          }
          return await fetch(url, init)
        }
      }

      const validate = async function (step, inform) {
        inform.setText('Teste den Endpunkt: ' + step.url)
        const response = await call(step.url, step.type, step.body)
        if (step.expected === 'SERVER_RETURNS_ERROR_CODE') {
          if (response.status < 200 || response.status > 299) {
            inform.setText(inform.getText() + '  - Erfolgreich')
            return true
          } else {
            inform.setText(inform.getText() + '  - Nicht Erfolgreich -  ' + 'Ein Validierungsfehler wurde erwartet!')
            return false
          }
        }
        if (step.expected === '') {
          inform.setText(inform.getText() + '  - Erfolgreich')
          return true
        } else {
          var parsedResponse = await response.json()

          var expected = JSON.stringify(step.expected)
          var current = JSON.stringify(parsedResponse.payload)
          if (expected === current) {
            inform.setText(inform.getText() + '  - Erfolgreich')
            return true
          } else {
            inform.setText(inform.getText() + '  - Nicht Erfolgreich -  ' + 'Unerwartete Antwort: ' + JSON.stringify(parsedResponse))
            return false
          }
        }
      }

