<template>
  <div class="mdc-layout-grid">
    <div class="mdc-layout-grid__inner">
      <div v-if="clerk === 0 && controller === 0" class="mdc-layout-grid__cell--span-12">
        <div class="mdc-card mdc-card--outlined text-card-content">
          <h4 class="mdc-typography--headline4">Login</h4>
          <div class="mdc-select mdc-select--outlined grid-cell-select">
            <i class="mdc-select__dropdown-icon"></i>
            <select v-model="clerk" class="mdc-select__native-control">
              <option value disabled selected></option>
              <option v-for="(clerk, index) in clerks" :key="index" :value="clerk.id">{{ clerk.firstName }} {{ clerk.lastName }}</option>
            </select>
            <div class="mdc-notched-outline">
              <div class="mdc-notched-outline__leading"></div>
              <div class="mdc-notched-outline__notch">
                <label class="mdc-floating-label">Angestellter der Flughafenverwaltung</label>
              </div>
              <div class="mdc-notched-outline__trailing"></div>
            </div>
          </div>
          <div class="mdc-select mdc-select--outlined grid-cell-select" style="margin-top: 1em">
            <i class="mdc-select__dropdown-icon"></i>
            <select v-model="controller" class="mdc-select__native-control">
              <option value disabled selected></option>
              <option v-for="(controller, index) in controllers" :key="index" :value="controller.id"
                >{{ controller.firstName }} {{ controller.lastName }}</option
              >
            </select>
            <div class="mdc-notched-outline">
              <div class="mdc-notched-outline__leading"></div>
              <div class="mdc-notched-outline__notch">
                <label class="mdc-floating-label">Fluglotse</label>
              </div>
              <div class="mdc-notched-outline__trailing"></div>
            </div>
          </div>
        </div>
      </div>
      <div v-if="clerk" class="mdc-layout-grid__cell--span-12">
        <Invoice />
      </div>
      <div v-if="controller" class="mdc-layout-grid__cell--span-12">
        <Schedule :controller="controller" :airplanes="airplanes" flight-status="LAND" />
      </div>
      <div v-if="controller" class="mdc-layout-grid__cell--span-12">
        <Schedule :controller="controller" :airplanes="airplanes" flight-status="TAKE_OFF" />
      </div>
    </div>
  </div>
</template>

<script>
import { MDCDataTable } from '@material/data-table'
import { MDCRipple } from '@material/ripple'
import { MDCTextField } from '@material/textfield'
import { MDCSelect } from '@material/select'
import Schedule from '../components/Schedule'
import Invoice from '../components/Invoice'

export default {
  components: { Invoice, Schedule },
  data () {
    return {
      clerk: 0,
      clerks: [],
      controller: 0,
      controllers: [],
      airplanes: []
    }
  },
  methods: {
    getClerks: async function () {
      const response = await fetch('http://localhost:8080/api/employees?job=AIRPORT_CLERK')
      const data = await response.json()
      this.clerks = data.payload
    },
    getControllers: async function () {
      const response = await fetch('http://localhost:8080/api/employees?job=AIR_TRAFFIC_CONTROLLER')
      const data = await response.json()
      this.controllers = data.payload
    },
    getAirplanes: async function () {
      const response = await fetch('http://localhost:8080/api/airplanes')
      const data = await response.json()
      this.airplanes = data.payload
    }
  },
  mounted () {
    for (const table of document.querySelectorAll('.mdc-data-table')) {
      MDCDataTable.attachTo(table)
    }
    for (const clickable of document.querySelectorAll('.mdc-button')) {
      MDCRipple.attachTo(clickable)
    }
    for (const textField of document.querySelectorAll('.mdc-text-field')) {
      MDCTextField.attachTo(textField)
    }
    for (const select of document.querySelectorAll('.mdc-select')) {
      MDCSelect.attachTo(select)
    }
    this.getClerks()
    this.getControllers()
    this.getAirplanes()
  }
}
</script>

<style>
@import "~@material/layout-grid/dist/mdc.layout-grid.css";
@import "~@material/card/dist/mdc.card.css";
@import "~@material/select/dist/mdc.select.css";

.grid-cell-select {
  width: 100%;
}

.text-card-content {
  padding: 16px;
}
</style>
